import {Component, ElementRef, EventEmitter, Input, Output, ViewEncapsulation} from '@angular/core';
import {AdaptAgreementState, AdaptContentAgreementText} from '../argeement.model';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../../common/radar/adapt-radar';import {KeyValueObject} from '../../common/common.models';
import {AdaptTranslateService} from '../../common/i18n';
import {formatString} from '../../common/utilities';

@Component({
  selector: 'adapt-agreement-content',
  styleUrls: ['../agreement.scss'],
  templateUrl: './agreement-content.component.html',
  encapsulation: ViewEncapsulation.None
})
export class AdaptAgreementContentComponent extends AdaptRadarAngularGenericDirective {
  isCollapsedPerformanceContent: boolean = true;
  isCollapsedMarketingContent: boolean = true;

  texts: AdaptContentAgreementText = {};

  @Input() multiProductUsage: boolean = false;

  @Input() state: AdaptAgreementState;

  @Input() showOrganizationSettings: boolean;

  @Input() showMarketingSection: boolean;

  @Output() readonly stateChange: EventEmitter<AdaptAgreementState> = new EventEmitter<AdaptAgreementState>();

  private _currentLanguage: KeyValueObject;

  constructor(private _ts: AdaptTranslateService,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptAgreementContent);

    this._currentLanguage = _ts.getCurrentLanguage();
    this._setDefaultTexts(this._currentLanguage);
  }


  handleStateChange(): void {
    if (this.showOrganizationSettings) {
      if (!this.state.organizationPerformance) {
        this.state.organizationMarketing = false;
        this.state.accountPerformance = false;
      }

      if (!this.state.organizationMarketing) {
        this.state.accountMarketing = false;
      }
    } else {
      if (!this.state.accountPerformance) {
        this.state.accountMarketing = false;
      }
    }

    this.stateChange.emit(this.state);
  }

  get learnMoreNoteText(): string {
    return formatString(this._currentLanguage['adapt.agreement.common.learnMoreNote'], this.multiProductUsage ? this._currentLanguage['adapt.agreement.common.learnMoreNoteMultiProductUsage'] : this._currentLanguage['adapt.agreement.common.learnMoreNoteNotMultiProductUsage']);
  }

  private _setDefaultTexts(translation: KeyValueObject): void {
    this.texts.settingsNote = translation['adapt.agreement.content.settingsNote'];
    this.texts.switchersMainTitle = translation['adapt.agreement.content.switchersMainTitle'];
    this.texts.organizationSwitcherLabel = translation['adapt.agreement.content.organizationSwitcherLabel'];
    this.texts.accountSwitcherLabel = translation['adapt.agreement.content.accountSwitcherLabel'];
    this.texts.performanceSwitcherLabel = translation['adapt.agreement.content.performanceSwitcherLabel'];
    this.texts.learnMoreLabel = translation['adapt.agreement.content.learnMoreLabel'];
    this.texts.learnMoreNoteNotMultiProductUsage = translation['adapt.agreement.common.learnMoreNoteNotMultiProductUsage'];
    this.texts.learnMoreNoteMultiProductUsage = translation['adapt.agreement.common.learnMoreNoteMultiProductUsage'];
    this.texts.learnMoreListItem1 = translation['adapt.agreement.common.learnMoreListItem1'];
    this.texts.learnMoreListItem2 = translation['adapt.agreement.common.learnMoreListItem2'];
    this.texts.learnMoreListItem3 = translation['adapt.agreement.common.learnMoreListItem3'];
    this.texts.learnMoreListItem4 = translation['adapt.agreement.common.learnMoreListItem4'];
    this.texts.learnMoreListItem5 = translation['adapt.agreement.common.learnMoreListItem5'];
    this.texts.learnMoreListItem6 = translation['adapt.agreement.common.learnMoreListItem6'];
    this.texts.learnMoreListItem7 = translation['adapt.agreement.common.learnMoreListItem7'];
    this.texts.learnMoreListItem8 = translation['adapt.agreement.common.learnMoreListItem8'];
    this.texts.learnMoreListItem9 = translation['adapt.agreement.common.learnMoreListItem9'];
    this.texts.learnMoreListItem10 = translation['adapt.agreement.common.learnMoreListItem10'];
    this.texts.learnMoreListItem11 = translation['adapt.agreement.common.learnMoreListItem11'];
    this.texts.learnMoreListItem12 = translation['adapt.agreement.common.learnMoreListItem12'];
    this.texts.learnMoreListItem13 = translation['adapt.agreement.common.learnMoreListItem13'];
    this.texts.learnMoreListItem14 = translation['adapt.agreement.common.learnMoreListItem14'];
    this.texts.marketingAndOutreachTitle = translation['adapt.agreement.content.marketingAndOutreachTitle'];
    this.texts.marketingAndOutreachSwitcherLabel = translation['adapt.agreement.content.marketingAndOutreachSwitcherLabel'];
    this.texts.marketingAndOutreachNote = translation['adapt.agreement.content.marketingAndOutreachNote'];
    this.texts.agreementNote = translation['adapt.agreement.common.agreementNote'];
  }
}
